

*{
    margin: 0;
    padding: 0;
    font-family: "Titillium Web", sans-serif;
}
li{
    text-decoration: none;
}
ul{
    list-style-type:none;
}
body{
   
}
input,input:focus{
    border: none;
     font-size: 25px;  
     max-width: 200px;
     height: 50px;

}
h1{
    padding: 20px;
    text-align: center;
}

.wrapper{
    margin:20px auto;
    background: #292d38;
    width: 750px;
    height:250px;
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    padding: 30px;
 justify-content: space-between;
 
 
}
.paragraf{
  
color: #92949c;


}
.block{
    padding-top:70px;
}
 @media (max-width:768px) {
    .wrapper{
     
    width: 700px;
    height: 200px; 
    padding: 20px;
    align-items: center; 
    }
    .input{
        height: 60px;
    padding:10px 15px 10px 15px;
    display:grid;
    grid-template-columns: 2fr 1fr;
    
    background: #fff;
  
    border-radius: 10px;
    }
    .button{
        width: 90px;
        height: 50px;
    }
    .block{
        width:320px;
        padding-top:20px;
    }
 }
 @media (max-width:541px){
    .wrapper{
        flex-direction: column;
        width: 400px;
        height: 270px;
    }
    .input{
        height: 70px;
    padding: 10px 20px 10px 20px;
    display:grid;
    grid-template-columns: 2fr 1fr;
    
    background: #fff;
  
    border-radius: 10px;
    }
    .block{
        width: 350px;
    }
    

 }
 @media (max-width:414px){ 
   
    .wrapper{
     width: 350px;
    justify-content: space-around;
    }
   .input{
    display:grid;
    grid-template-columns: 2fr 1fr;
    background: #fff;
    height: 60px;
    padding: 10px;
    border-radius: 10px;
    }
    .block{
        padding-top:20px;
    }
    
 }


.button{
    background: #fff;
    width: 100px;
    height: 50px;
    border-radius: 5px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
}
.button:hover{
    background: #f5eded;
    
}
@media (min-width:769px){
.input{
    display:grid;
    grid-template-columns: 2fr 1fr;

    background: #fff;
    height: 70px;
    padding: 10px 30px  10px 30px;
    border-radius: 10px;

}
.block{
width:370px;



}
}

.links{
    background: #f5eded;
    padding: 10px;
    width: 50%;
    margin-left:44%;
    overflow-y: scroll;
    height: 230px;
    margin-top: -20px;

}
.flag{
  
    

}
.name{

}
.arrow{
    transition: transform 0.3s ease;
}
.arrow:active{
  transform: rotate(180deg);
}
.list{
    padding: 10px;
font-size: 12px;
    color: gray;
    display: grid;
    grid-template-columns:repeat(2,1fr) ;
    grid-template-rows:0.5fr ;
    
   
}
.list>p{
    font-size: 25px;
    color: black;
     margin-bottom: 8px;
}
.list:hover{
    background: #fff;
}
